:root {
  // base

  --font-family: "Poppins";
  --content-width: 1350px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors

  --primary: #333333;
  --blue: #25AAE2;
  --darkBlue: #15759D;
  --whiteColor: #ffffff;
}
